.homeTab {
  aspect-ratio: 2360/1640;
  display: flex;
  flex-direction: column;
  border-color: black;
  border-width: 0 0 1px 0;
  position: relative;
  align-items: center;
}

.name {
  font-size: 120px;
  color: white;
  position: relative;
  text-align: center;
  font-family: Futura;
  font-weight: bold;
}

.spaceShip {
  position: absolute;
  height: 60%;
  margin-top: 14.2%;
  margin-left: 0.6%;
}

.movingFire {
  position: absolute;
  height: 60%;
  margin-top: 26%;
}

.shadowClipDiv {
  position: absolute;
  width: 100%;
  aspect-ratio: 2360/1640;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  overflow: clip;
 }


.homeBackground {
  position: absolute;
  width: 100%;
  aspect-ratio: 2360/1640;
}

.homeBorderDiv {
  margin-top: 63.3%;
  position: absolute;
}

.homeBorder {
  width: 100%;
  display: block;
}

.sun {
  position: absolute;
  width: 50%;
  margin-right: -50%;
}

.moon {
  position: absolute;
  width: 35%;
  margin-top: 5%;
  margin-left: -60%;
}

.shadow {
  position: absolute;
  width: 35%;
  margin-top: 48%;
  margin-left: -27.5%;
}