.landingTab {
  background-color: #121212;
  min-height: 100vh;
}

.landerClipDiv {
  position: absolute;
  width: 100%;
  aspect-ratio: 2320 / 2657;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  overflow: clip;
 }
.landerFlex {
  display: flex;
  flex-direction: column;
  aspect-ratio: 2320 / 2657;
}

.landerSpacer {
  height: 60%;
}

.landerHorzFlex {
  display: flex;
  flex-direction: row;
  height: 25%;
}

.myFaceHolder {
  width: 50%;
}

.linksHolder {
  width: 50%;
}

.link {
  font-size: 2vw;
  padding-left: 40%;
  text-align: left;
  height: 15%;
  color: white;
  position: relative;
  font-family: Futura;
  font-weight: bold;
}

.landerName {
  font-size: 12vw;
  height: 15%;
  color: white;
  position: relative;
  text-align: center;
  font-family: Futura;
  font-weight: bold;
 }

 .blueBorder2 {
  margin-top: -0.5%;
  position: absolute;
  width: 100vw;
  left: 0;
  scale: -1;
}

 .landingSite {
  position: absolute;
  width: 100%;
  left: 0;
}

.landingRocket {
  position: absolute;
  width: 10%;
}

