.landingFire {
  position: absolute;
  width: 10%;
}

.landingShadow {
  position: absolute;
  width: 10%;
  animation: landingShadow linear both;
  animation-timeline: scroll();
  animation-range-start: 82%;
  animation-range-end: 91%;
}

@keyframes landingShadow {
  0% {
    transform: translate(-130%, 1260%) rotate(0deg) scale(3);
  }
  90% {
    transform: translate(-130%, 1260%) rotate(0deg) scale(3);
  }
  95% {
    transform: translate(-130%, 1270%) rotate(0deg) scale(3);
  }
  100% {
    transform: translate(-130%, 1310%) rotate(0deg) scale(3);
  }
}