.goalsTab {
  background-color: #03000a;
  border: solid;
  border-color: black;
  border-width: 0 0 1px 0;
  aspect-ratio: 2360 / 1640;
  width: 100%;
}

.goalsTitle {
  font-size: 5vw;
  color: #3d93bd;
  text-align: left;
  font-family: Futura;
  font-weight: bold;
  float: left;
  margin-top: 3%;
}

.goalsCircle {
  background-color: #3d93bd;
  border-radius: 50%;
  aspect-ratio: 1/1;
  width: 12%;
  margin: 6% 2% 6% 2%;
}

.rightGoalsCircle {
  background-color: #3d93bd;
  border-radius: 50%;
  aspect-ratio: 1/1;
  width: 12%;
  margin: 6% 4% 6% 2%;
}

.goalsBorder {
  border: solid;
  border-color: #3d93bd;
  border-width: 0 0 3px 0;
  margin: 0 50px 0 50px;
  position: relative;
}

.goalsParagraph {
  font-size: 2.25vw;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  text-align: left;
}

.spaceHolder {
position: relative;
}

.goalsFlex {
  display: flex;
  flex-direction: row;
  margin: 10% 7.5% 0 7.5%;
  position: absolute;
}

.goalsTextBoxHolder {
  width: 50%;
}

.goalsTextBox {
  margin: 20% 0 0 20%;
  padding: 15px;
  background-color: #3d93bdbd;
  border-radius: 30px;
  transition: transform 0.15s ease-in-out;
}

.goalsTextBox:hover {
  transform: scale(1.1);
  background-color: #3d92bd;
}

.planetDiv {
  position: relative;
  width: 50%;
}

.pinkClipDiv {
  margin-top: 40%;
  width: 100%;
  position: absolute;
  clip-path: circle(40% at 60% 80%);
}

.pinkPlanet {
  margin: -35% 0 0 -50%;
  position: absolute;
  width: 120%;
}

.pinkShadingHolder {
  position: absolute;
  width: 50%
}

.pinkShading {
  position: absolute;
  margin-left: 13.2%;
  margin-top: 23.2%;
  width: 113.4%;
}

.pinkGlow {
  width: 300%;
  margin-left: -80%;
  margin-top: -22%;
  position: absolute;
}

.yellowClipDiv {
  margin-top: 90%;
  margin-left: 75%;
  width: 55%;
  position: absolute;
  clip-path: circle(40% at 60% 80%);
}

.yellowPlanet {
  margin: -35% 0 0 -50%;
  position: absolute;
  width: 200%;
}

.yellowShadingHolder {
  position: absolute;
  width: 50%
}

.yellowShading {
  position: absolute;
  margin-left: 135%;
  margin-top: 148.5%;
  width: 62.5%;
}

.yellowGlow {
  width: 120%;
  margin-left: 104%;
  margin-top: 121%;
  position: absolute;
}

.spaceBackground {
  width: 100%;
  display: block;
  position: absolute;
 }

 .asteroidsClipDiv {
  position: absolute;
  width: 100%;
  aspect-ratio: 2360 / 1640;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  overflow: clip;
 }

 .spaceShipClipDiv {
  position: absolute;
  width: 100%;
  aspect-ratio: 2360 / 1640;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  overflow: clip;
 }

 .asteroids {
  position: absolute;
  width: 50%;
  margin-top: 25%;
  margin-left: -20%;
  transform-origin: 55% 20%;
 }

.clouds {
  position: absolute;
  width: 100%;
  left: 0;
  margin-top: -35%;
}

.goalsBorderDiv {
  position: relative;
  width: 100vw;
}

.pinkBorder {
  margin-top: 63.5%;
  position: absolute;
  width: 100vw;
  left: 0;
}

.spaceShip2 {
  position: absolute;
  width: 10%;
}

.fire {
  position: absolute;
  width: 10%;
}

.fuelTank {
  position: absolute;
  width: 10%;
}

.leftBooster {
  position: absolute;
  width: 10%;
}

.rightBooster {
  position: absolute;
  width: 10%;
}